

.track {
    width:250px;
    height:250px;
    position: relative;
    display: inline-block;
    margin:20px;
    cursor: pointer;
}

.track-image {
    width:250px;
    height:250px;
}

.track-text {
    position: absolute;
    width:250px;
    bottom:0;
    background-color: black;
    color: white;
    padding: 5px;
}

.track-icon {
    position:absolute;
    bottom:100px;
    left:100px;
    color: white;
    background:black;
    width:50px;
    height:50px;
    border-radius: 25px;
    padding:10px;
    opacity:0.8;
}